.TopPage {
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;

}

.LeftText,
.RightIMG {
  width: 50%;
  height: 100%;
  padding: 5vh 5vw;
  box-sizing: border-box;
}

.LeftText h1 {
  font-size: 4vh;
  letter-spacing: 0.2vw;


}

.RightIMG {
  text-align: center;
}

.RightIMG h1 {
  font-size: 1.5vh;
  color: white;
  margin: 0;
  letter-spacing: 0.15vw;
  padding: 0;
}

.RightIMG img {
  transform: scaleX(-1);
  height: 60vh;
  filter: grayscale(80%);

  width: auto;
}